@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
    @apply px-2;
}

.bg-primary {
    background-color: #E4E2DD;
}

.bg-secondary {
    background-color: #99235C;
}

.bg-light {
    background-color: #f17db5;
}

.text-custom {
    color: #99235C;
}

.text-light {
    color: #f17db5;
}

.text-beige {
    color: #E4E2DD;
}

.mobile-nav {
    @apply fixed top-10 left-0 w-full h-1/6;
}

.mobile-nav li {
    @apply w-full h-full text-right mt-2;
}

html {
    scroll-behavior: smooth;
}